<template>
    <div class='handle'>
        <el-dialog
            title="小一课时处理"
            v-dialogDrag
            :visible.sync="studentTableThis.stuHandleShow"
            :before-close="handleClose">
            <section>
                <div class='handletop'>
                    <span class='titleClass'>本课时即将处理为请假，并对本次课时进行以下操作：</span>
                    <ul class='ChoiceList'>
                        <li v-for='item in radioCorrList' :key='item.id'>
                            <label @click.stop="changeRadio(item.id)">
                                <input type="radio" :checked='item.f'>
                                <span>
                                    <em>{{item.sinfo_one}}</em>{{item.sinfo_two}}
                                </span>
                            </label>
                        </li>
                    </ul>
                </div>
                <div class='handlebottom'>
                    <span>备 注：</span>
                    <el-input type='textarea' maxlength='500' v-model='remarks'></el-input>
                </div>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button size='small' type="primary" @click="stubmitClick">保 存</el-button>
                <el-button size='small' @click="studentTableThis.stuHandleShow = false">取 消</el-button>
            </span>
            <div class='xxx-tk'>
                <header class='xxx-header'>
                    <span>调整上课时间</span>
                </header>
                <main class='xxx-main'>
                    111
                </main>
                <footer class='xxx-footer'>
                    <el-button size='small' type="primary">确 认</el-button>
                    <el-button size='small' >取 消</el-button>
                </footer>
            </div>
        </el-dialog>

    </div>
</template>
<script>
export default {
  name: 'stydentHandle',
  data () {
    return {
      radioCorrList: [{id:1,sinfo_one: '自动顺延',sinfo_two: '(将本节课置于本次排课序列的最后)',f:true},
      {id:2,sinfo_one: '调整上课时间',sinfo_two: '(进行调课处理)',f:false},
      {id:3,sinfo_one: '待处理',sinfo_two: '(稍后处理，将本节课时移入待处理列表中)',f:false}],
      remarks: ''
    }
  },
  inject:['studentTableThis'],
  methods: {
    changeRadio(id){
        this.radioCorrList.forEach((data) => {
            if (data.id == id) {
                data.f = true
            } else {
                data.f = false
            }
        })
    },
    stubmitClick() {
      // 保存接口

    }
  }
}
</script>
