<template>
    <div class='studentTable_conenter'>
        <el-table
            :data="studentThis.tableData"
            stripe
            border
            style="width: 100%">
            <el-table-column
            label="学生"
            align='center'
            min-width='7.7%'>
              <template slot-scope="scope">
                  <span class='xx-color'>
                      {{scope.row.studentName}}
                  </span>
              </template>
            </el-table-column>
            <el-table-column
            prop="code"
            label="类别"
            align='center'
            min-width='7.7%'>
            </el-table-column>
            <el-table-column
            prop="kc"
            label="课程"
            align='center'
            min-width='11.11%'>
            </el-table-column>
            <el-table-column
            prop="sf"
            label="收费"
            align='center'
            min-width='7.7%'>
            </el-table-column>
            <el-table-column
            prop="js"
            label="教师"
            align='center'
            min-width='7.7%'>
            </el-table-column>
            <el-table-column
            prop="Stime"
            align='center'
            label="上课时间"
            min-width='11.11%'>
            </el-table-column>
            <el-table-column
            prop="skTime"
            label="消课时间"
            align='center'
            min-width='11.11%'>
            </el-table-column>
            <el-table-column
            prop="skqk"
            label="消课情况"
            align='center'
            min-width='7.7%'>
            </el-table-column>
            <el-table-column
            prop="sykc"
            label="剩余课时"
            align='center'
            min-width='7.7%'>
            </el-table-column>
            <el-table-column
            prop="remk"
            align='center'
            label="备注"
            min-width='7.7%'>
            </el-table-column>
            <el-table-column
            label="操作"
            align='center'
            min-width='12.2%'>
                <template slot-scope="scope">
                    <el-button type="text" class='xx-color' size="small" @click.stop='infoClick'>详情</el-button>
                    <el-button type="text" class='xx-color' size="small" v-if='scope.row.skTime!=""'>取消消课</el-button>
                    <el-button type="text" class='xx-color' size="small" v-else @click.stop='HandleClick'>处理</el-button>
                </template>
            </el-table-column>
        </el-table>
        <erpPaging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize"/>
        <transition name='stu' >
          <studentinfo v-show='shoppclassInfoShow'/>
        </transition>
        <studHanle/>
    </div>
</template>
<script>
import erpPaging from '../../../../paging/pagingShort'
import studentinfo from './studentInfo/studentInfo'
import studHanle from './stydentHandle/stydentHandle'
export default {
  name: 'studentTable',
  data () {
    return {
      shoppclassInfoShow: false,
      stuHandleShow: false,
      countNum: 0,
      page_num: 0,
      pageSize: 8
    }
  },
  inject: ['studentThis'],
  provide(){
    return {
      studentTableThis: this
    }
  },
  methods: {
    paging_father(i){
      // 分页
    },
    infoClick () {
      // 详情
      this.shoppclassInfoShow = !this.shoppclassInfoShow
    },
    HandleClick () {
      // 处理
      this.stuHandleShow = !this.stuHandleShow
    }
  },
  components: {
    studentinfo: studentinfo,
    erpPaging: erpPaging,
    studHanle: studHanle
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.stu-enter,
.stu-leave-to,
.stu-enter-to,
.stu-leave{
    transition: all 0.3s ease;
}
.stu-enter,
.stu-leave-to{
  opacity: 0;
}
.stu-enter-to,
.stu-leave{
    opacity: 1;
}
.studentTable_conenter{
  padding: 0 vw(45);
  box-sizing: border-box;
  .xx-color{
      color: #2769fb;
  }
}
</style>