<template>
    <div class='CwA_conenter'>
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-menu-item index="1">
                学生考勤
                <i class='is-position' v-if='activeIndex == "1"'></i>
            </el-menu-item>
            <el-submenu index="2">
                <template slot="title">
                    <span>
                        {{menuItemName}}
                        <i class='is-position' v-if='activeIndex == "2-1" || activeIndex == "2-2" || activeIndex == "2-3" '></i>
                    </span>
                    <i class='el-submenu__icon-arrow el-icon-caret-bottom'></i>
                </template>
                <el-menu-item :index="item.id" v-for='item in menuItem' :key='item.id'>{{item.name}}</el-menu-item>
            </el-submenu>
        </el-menu>
        <stuCwA v-if='activeIndex == "1"'/>
    </div>
</template>
<script>
import stuCwA from './StudentCwA/StudentCwA'
export default {
  name: 'Check_work_attendance',
  data () {
    return {
      activeIndex: "1",
      menuItemName: '员工考勤',
      menuItem: [{id: '2-1',name: '员工考勤统计'},{id: '2-2',name: '考勤设置'},{id: '2-3',name: '员工管理'}]
    }
  },
  methods: {
    handleSelect(key, keyPath){
      //  tab切换导航
      const _this = this
      this.activeIndex = key
      if (key == '2-1' || key == '2-2' || key == '2-3'){
        this.menuItem.forEach((data) => {
            if (data.id == key) {
                _this.menuItemName = data.name
            }
        })
      }else if (key == '1'){
        _this.menuItemName = '员工考勤'
      }
    }
  },
  components: {
    stuCwA: stuCwA
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
.CwA_conenter /deep/ .el-menu-demo {
  padding: 0 vw(20);
  box-sizing: border-box;
  height: 100%;
  li {
    color: #333333;
    font-weight: 500;
    font-family: SourceHanSansCN-Bold;
    position: relative;
    border-bottom-color: #fff!important;
    border-bottom:none;
    font-size: 16px;
    .is-position{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0 auto;
        @include wh(vw(30),2px);
        @include bgColor-defaul($fn-color);
    }
    .el-submenu__title{
        color: #333333;
        font-weight: 500;
        font-family: "SourceHanSansCN-Bold";
        border-bottom-color: #fff!important;
        border-bottom:none;
        span{
            position: relative;
            @include wh(auto, 100%);
            display: inline-block!important;
        }
        .el-submenu__icon-arrow{
            font-size: vw(24);
            margin-left: vw(4);
        }
        .el-submenu__icon-arrow:last-child{
            display: none;
        }
    }

  }
  .is-active .el-submenu__title{
      @include fontColor-defaul($fn-color!important);
      border-bottom: none;
      background: none;
   }
  .is-active{
    @include fontColor-defaul($fn-color!important);
    border-bottom: none;
    background: none;
  }
}
</style>