<template>
    <div class="Shop_items">
        <el-dialog
            title="购物"
            v-dialogDrag
            :visible.sync="tablesThis.shopitemsShow"
            width="30%">
            <section class='_shop_items'>
                 <el-form :model="ruleForm" ref="ruleForm" label-width="21%" class="demo-ruleForm">
                    <el-form-item label="学生姓名" prop="name">
                        :<el-input v-model="ruleForm.stu_name" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*销售员类型" prop="name">
                        : <section class='radio_class'>
                            <div class='radio-label' v-for='item in form.Sales_type' :key='item.id' @click.stop='changRadio(item.id)'>
                                <input type="checkbox" :checked='item.f'>
                                <span>{{item.name}}</span>
                            </div>
                        </section>
                    </el-form-item>
                    <el-form-item label="*销  售  员" prop="name">
                        :<el-select v-model="ruleForm.personnel"  placeholder="请选择方式">
                            <el-option label="请选择" value='0'></el-option>
                            <el-option
                            v-for='item in form.personnel_form'
                            :key="item.sm_id"
                            :label="item.sm_name"
                            :value="item.sm_id"
                             v-if='form.Sales_type[0].f == true'>
                            </el-option>
                            <el-option
                            v-for='item in form.personnel_form'
                            :key="item.t_id"
                            :label="item.t_name"
                            :value="item.t_id"
                            v-else-if='form.Sales_type[1].f == true'>
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="*销 售 商 品" prop="name">
                        :<el-select v-model="form.personnel_way"
                                multiple
                                filterable
                                allow-create
                                default-first-option
                                @change='selectChange'
                                placeholder="请选择方式">
                            <el-option
                            v-for="item in form.personnel_way_form"
                            :key="item.goods_id"
                            :label="item.goods_name"
                            :value="item.goods_id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <section class='div_gulptable'>
                        <el-table
                            :data="form.table_personnel_way"
                            border
                            style="width: 100%; height:auto;">
                            <el-table-column
                            align='center'
                            label="商品名称"
                            min-width="25.5%">
                            <template slot-scope="scope">
                                {{scope.row.goods_name}}
                            </template>
                            </el-table-column>
                            <el-table-column
                            align='center'
                            label="单价(元)"
                            min-width="22%">
                            <template slot-scope="scope">
                                <el-input size='small' v-model="scope.row.goods_amount" @keyup.native="PriceCal(scope.row.goods_id,0)" placeholder="单价"></el-input>
                            </template>
                            </el-table-column>
                            <el-table-column
                            align='center'
                            label="数量(台)"
                            min-width="22%">
                            <template slot-scope="scope">
                                <el-input size='small'  v-model="scope.row.goods_platform" @keyup.native="PriceCal(scope.row.goods_id,1)" placeholder="数量"></el-input>
                            </template>
                            </el-table-column>
                            <el-table-column
                            align='center'
                            label="总价(元)"
                            min-width="22%">
                            <template slot-scope="scope">
                                <el-input size='small' v-model="scope.row.goods_total" @keyup.native="PriceCal(scope.row.goods_id,2)" placeholder="总价"></el-input>
                            </template>
                            </el-table-column>
                        </el-table>
                    </section>
                    <el-form-item label="实付款" prop="name">
                        :<el-input v-model="form.ActualPay" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*付 款 方 式" prop="name">
                        :<el-select v-model="ruleForm.payway" placeholder="选择方式">
                            <el-option
                            v-for="item in form.payway"
                            :key="item.pay_id"
                            :label="item.pay_name"
                            :value="item.pay_id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="销售时间" prop="name">
                        :<el-date-picker
                            v-model="form.time"
                            type="date"
                            :editable='false'
                            prefix-icon="el-icon-caret-bottom"
                            placeholder="选择时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="备注" prop="name">
                        :<el-input type='textarea' placeholder='可填写备注信息' v-model="ruleForm.payment" maxlength="500" show-word-limit></el-input>
                    </el-form-item>
                 </el-form>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="tablesThis.shopitemsShow = false" size='small'>确 定</el-button>
                <el-button @click="tablesThis.shopitemsShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getSalesmanList,xs_shopCommodity} from '../../../../../getserver/server'
import {selectedTeacher,all_pay_list} from '../../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
  name: 'shop_items',
  data () {
    return {
      dialogVisible: true,
      form: {
        table_personnel_way: [],// 商品列表
        personnel_way_form: [],
        personnel_way: [],
        personnel_form: [], // 销售员
        Sales_type: [
            {id:1,name:'销售员',f: true},
            {id:2, name:'教师',f: false}
        ], // 销售员类型
        payway: [], // 付款方式
        ActualPay: '',// 实付款
        time: '' // 销售时间
      },
      ruleForm: {
        name: '傻逼',
        personnel: '0',
        payment: '',
        payway: '',// 选中的付款方式
      }
    }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    selectChange(key){
        let keyArr = key
        let activeArr = []  // 选中的 select
        const _this = this
        this.form.personnel_way_form.forEach((perDate) => {
            keyArr.forEach((keydata)=>{
                if (keydata == perDate.goods_id) {
                  activeArr.push({goods_id: perDate.goods_id, goods_name: perDate.goods_name, goods_amount: perDate.goods_amount, goods_platform: 0, goods_total: 0})
                }
            })
        })
        if (activeArr.length >= _this.form.table_personnel_way.length) {
            // 添加
            activeArr.forEach((data)=>{
                let statusZt = dupRem(data.goods_id, _this.form.table_personnel_way)
                if (!statusZt) {
                    _this.form.table_personnel_way.push(data)
                }
            })
        } else if (activeArr.length  < _this.form.table_personnel_way.length) {
            // 删除
             _this.form.table_personnel_way.forEach((data,index)=>{
                let statusZt = dupRem(data.goods_id, activeArr)
                if (!statusZt) {
                    _this.form.table_personnel_way.splice(index, 1);
                }
             })
        }
        function dupRem(id,Arrs) {
          // 去重
          let status = false;
          Arrs.forEach((_data)=>{
              if (_data.goods_id == id) {
                status = true
              }
          })
          return status
        }
    },
    async all_pay_list_fun () {
        // 获取全部付款方式列表
        let resDate = await all_pay_list(this.userLoginInfo.orgid);
        if (resDate.data.status) {
            // 获取成功
            this.form.payway = resDate.data.data
        }
    },
    changRadio(id){
      const _this = this
      this.form.Sales_type.forEach((data)=>{
        if (data.id == id) {
          data.f = true
          if (id == 1) {
            _this.getSalesmanList_fun()
            _this.ruleForm.personnel = '0'
          } else if (id == 2) {
            _this.getselectedTeacher()
            _this.ruleForm.personnel = '0'
          }
        } else {
          data.f = false
        }
      })
    },
    Time_fun(){
      let DateTime = new Date()
      let Y = DateTime.getFullYear();// 年
      let M = DateTime.getMonth() + 1;// 月
      let D = DateTime.getDate();// 日
      this.form.time = Y +'-'+ M + '-' + D
    },
    getSalesmanList_fun(){
        const _this = this
        getSalesmanList(this.userLoginInfo.orgid).then((res)=>{
            if (res.data.status) {
                let Arr = res.data.data;
                Arr.forEach((data) => {
                    data['f'] = false
                })
                _this.form.personnel_form = Arr
            }
        })
    },
    getselectedTeacher(){
      const _this = this
      selectedTeacher(this.userLoginInfo.orgid).then((res)=>{
           if (res.data.status) {
                let Arr = res.data.data;
                Arr.forEach((data) => {
                    data['f'] = false
                })
                _this.form.personnel_form = Arr
            }
      })  
    },
    get_xs_shopCommodity(){
        const _this = this
        xs_shopCommodity(this.userLoginInfo.orgid).then((res)=>{
            if(res.data.status) {
                _this.form.personnel_way_form = res.data.data.data
            }
        })
    },
    PriceCal(id,i){
      const _this = this
      let actualPrice = ""
      loopWatch(this.form.table_personnel_way,id,i)
      function loopWatch(Arr,id,str){
        let num = 0;// 存储实付款金额
        Arr.forEach((item,_index) => {
            if (item.goods_id == id) {
              if (str == 0) {
                // 单价 - 计算总价
                actualPrice = item.goods_amount;
                if(typeof(actualPrice) != 'number'){
                    //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
                    actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
                    actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
                    actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
                    actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
                    if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                        actualPrice= parseFloat(actualPrice);  
                    }
                    _this.form.table_personnel_way.goods_amount = item.goods_amount = actualPrice == ""?0:actualPrice
                }
                if (item.goods_platform !=0) {
                    // 数量不等0时进行 计算 总价 以及实付款项
                    item.goods_total = item.goods_platform * item.goods_amount // 数量成语单价 等于总价
                }
              } else if (str == 1){
                // 数量 - 计算总价
                if(!/\d+$/g.test(item.goods_platform)){ // 正则验证
                    _this.form.table_personnel_way.goods_platform = item.goods_platform = item.goods_platform.substring(_index, item.goods_platform.length-1)==""?0:item.goods_platform.substring(_index, item.goods_platform.length-1)
                }
                if (item.goods_amount != 0) {
                    // 数量不等0时进行 计算 总价 以及实付款项
                    item.goods_total = item.goods_platform * item.goods_amount // 数量成语单价 等于总价
                }
              } else if (str == 2) {
                  // 总价的变化
                actualPrice = item.goods_total;
                if(typeof(actualPrice) != 'number'){
                    //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
                    actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
                    actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
                    actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
                    actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
                    if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                        actualPrice= parseFloat(actualPrice);  
                    }
                    _this.form.table_personnel_way.goods_total = item.goods_total = actualPrice == "" ? item.goods_platform * item.goods_amount : actualPrice
                }
                item.goods_total = parseFloat(item.goods_total)
              }
            }
            num += item.goods_total; // 循环所有商品的单价，进行累加，计算出实际付款金额
        })
        _this.form.ActualPay = num; // 赋值实际付款
      }
    }
  },
  inject: ['tablesThis'],
  watch:{
    'tablesThis.ShopItemDate'(val) {
      if (val != null) {
        this.ruleForm = val
        this.getSalesmanList_fun()
        this.get_xs_shopCommodity()
        this.all_pay_list_fun()
        this.Time_fun()
      }
    },
    'tablesThis.shopitemsShow' (val) {
      if (val == false) {
        this.tablesThis.ShopItemDate = null
        this.form.personnel_way = []
        this.form.personnel_way_form = []
        this.form.table_personnel_way = []
      }
    }
  }
}
</script>
