                    line-height: normal;
                }
                .el-dialog__headerbtn{
                    top: vw1(20);
                    .el-dialog__close{
                    color: #ffff;
                    }
                }
            }
            .el-dialog__body{
                flex: 1;
                overflow-y:auto;
                ._shop_items{
                        .el-form{
                            .el-form-item{
                                height: vw1(40);
                                .el-form-item__label{
                                    height: 100%;
                                    text-align: justify;
                                    font-size: vw1(16);
                                    color: #333333;
                                }
                                .el-form-item__label::after{
                                    content: " ";
                                    display: inline-block;
                                    width: 100%;
                                }
                                .el-form-item__content{
                                    display: flex;
                                    .radio_class{
                                        flex: 1;
                                        margin-left: vw1(8);
                                        display: flex;
                                        .radio-label{
                                            font-size: vw1(16);
                                            display: flex;
                                            align-items: center;
                                            color: #999999;
                                            margin-right: vw1(14);
                                            cursor: pointer;
                                            input[type=checkbox]{
                                                cursor: pointer;
                                                background: #fff;
                                                -webkit-appearance:none;
                                                border: 2px solid #999999;
                                                border-radius: 2px;
                                                outline: none;
                                                @include wh(14px,14px);
                                                margin-right: vw1(9);
                                            }
                                            input[type=checkbox]:checked{
                                                background-size: 100%;
                                                background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                                            }
                                        }
                                        .radio-label:last-child{
                                            margin-right: 0;
                                        }
                                    }
                                    .el-select .el-input{
                                        margin-left: 0;
                                    }
                                    .el-input,.el-textarea,.el-select{
                                        margin-left: vw1(8);
                                        .el-textarea__inner{
                                            height: vw1(100);
                                            resize: none;
                                        }
                                        .el-input__count{
                                            line-height: normal;
                                        }
                                        .el-input__inner[disabled=disabled]{
                                            background: #fff;
                                            // border:none;
                                        }
                                        .el-input__inner{
                                            height: vw1(40);
                                        }
                                    }
                                    .el-date-editor{
                                        width: 100%;
                                        position: relative;
                                        .el-input__prefix{
                                            // right: vw(10);
                                            left: 91%;
                                            font-size: vw1(25);
                                            color: #999999;
                                        }
                                        .el-input__suffix{
                                            display: none;
                                        }
                                    }
                                }
                            }
                            .div_gulptable{
                                @include wh(100%,auto);
                            }
                            .div_gulptable /deep/ .el-table {
                                height: 540px;
                                background-color: #ffffff;
                                box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
                                .el-table__body-wrapper {
                                    @include bgColor-defaul($bgColor-color)
                                }
                            }
                        }
                    }
            }
            .el-dialog__footer{
                @include wh(100%, vw1(80));
                .dialog-footer{
                    button{
                        @include wh(vw1(120), auto);
                        font-size: vw1(16);
                        background-color: #e7eaec;
                        color: #999999;
                    }
                    button:first-child{
                        @include bg-defaul($bg-color);
                        color: #fff;
                    }
                }
            }
        }
    }
  }
}
